import React from 'react';
import BlogCard from '../BlogCard/BlogCard';
import { getBlogContent } from '../../content/content_blog';
import { useLanguage } from '../../context/LanguageContext';
import './BlogSection.css';

interface BlogSectionProps {
    content?: any;
}

const BlogSection: React.FC<BlogSectionProps> = ({ content }) => {
    const { lang } = useLanguage();
    const defaultContent = getBlogContent(lang);

    const sectionTexts = content?.blogSectionContent || defaultContent.blogSectionContent;

    const rawPosts = content?.blogPosts || defaultContent.blogPosts;

    const featuredPosts = React.useMemo(() => {
        if (!rawPosts) return [];
        return rawPosts
            .map((post: any) => ({
                ...post,
                image: post.image || post.seo?.ogImage || `https://images.unsplash.com/photo-1558618666-fcd25c85cd64?w=1200&h=800&fit=crop&q=80`
            }))
            .filter((post: any) => post.featured)
            .slice(0, 3);
    }, [rawPosts]);

    return (
        <section className="blog-section">
            <div className="blog-section__overlay"></div>

            <div className="blog-section__inner">
                {/* Section Header */}
                <div className="blog-section__header">
                    <span className="blog-section__eyebrow">{sectionTexts.eyebrow}</span>
                    <h2 className="blog-section__title">
                        <span className="blog-section__title-main">{sectionTexts.titleMain}</span>
                        <span className="blog-section__title-sub">{sectionTexts.titleSub}</span>
                    </h2>
                    <p className="blog-section__subtitle">
                        {sectionTexts.subtitle}
                    </p>
                </div>

                {/* Blog Cards Grid */}
                <div className="blog-section__grid">
                    {featuredPosts.map((post: any, index: number) => (
                        <div
                            key={post.id}
                            className="blog-section__card"
                            style={{
                                animation: `fadeInUp 0.8s ease-out ${0.1 * (index + 1)}s both`
                            }}
                        >
                            <BlogCard
                                title={post.title}
                                description={post.description}
                                author={post.author}
                                date={post.date}
                                image={post.image}
                                category={post.category}
                                slug={post.slug}
                            />
                        </div>
                    ))}
                </div>

                {/* View All CTA */}
                <div className="blog-section__cta-wrapper">
                    <a href="/blog" className="blog-section__cta">
                        <span>{sectionTexts.viewAllCta}</span>
                        <svg
                            className="blog-section__cta-arrow"
                            width="24"
                            height="24"
                            viewBox="0 0 24 24"
                            fill="none"
                        >
                            <path
                                d="M5 12H19M19 12L12 5M19 12L12 19"
                                stroke="currentColor"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                            />
                        </svg>
                    </a>
                </div>
            </div>
        </section>
    );
};

export default BlogSection;
